<template>
  <div style="margin: 30px 0 0 0">
    <div class="gonggao bdb_d fix">
      <div class="txtScroll-top l fix zy_cur">
        <div class="l tit">
          <b class="dib vm">当前位置：</b>
        </div>
        <p class="l">
          <router-link to="/index">首页</router-link>
          <span>教学名师</span>
        </p>
      </div>
    </div>

    <div class="zy_contentbox">
      <div class="zy_content fix">
        <div class="zy_left_con">
          <div class="zy_twonav">
            <h2 class="tit">名师列表</h2>
            <ul>
              <li class="cur">
                <a href="javascript:void(0)" @click="loadData(1)">教学名师</a>
              </li>
            </ul>
          </div>
        </div>

        <div class="zy_right_con">
          <div v-if="currpage == 'list'">
            <div class="zy_title fix">
              <h2><i></i>教学名师</h2>
            </div>
            <div class="zy_peoplebox">
              <ul>
                <li v-for="(item, index) in list">
                  <a href="javascript:;" @click="toDetail(item.id)">
                    <i><img :src="item.photoUrl" /></i>
                    <p>
                      <b>{{ item.teacherName }}</b>
                      <span>职称: {{ item.title }}</span>
                      <span>荣誉: {{ item.post }}</span>
                      <span>{{ item.email }}</span>
                    </p>
                  </a>
                </li>
              </ul>
            </div>
            <div class="fanye" style="text-align: left">
              <a
                href="#"
                :class="{
                  customdisable: !page.hasPreviousPage,
                }"
                @click="loadData(page.prePage)"
                >上一页</a
              >
              <div v-for="(item, index) in page.navigatepageNums">
                <a
                  href="#"
                  :class="{ current: item == page.pageNum }"
                  @click="loadData(item)"
                  >{{ item }}</a
                >
              </div>
              <a
                href="#"
                :class="{ customdisable: !page.hasNextPage }"
                @click="loadData(page.nextPage)"
                >下一页</a
              >
              <p>
                <span>共{{ page.pages }}页</span>
              </p>
            </div>
          </div>

          <div v-if="currpage == 'detail'">
            <div class="zy_content fix">
              <div class="zy_conbox">
                <div class="zy_newsxq">
                  <div class="xq_tit bdb_d">
                    <img :src="article.photoUrl" class="img" />
                    <div class="infoDiv">
                      <div class="infoWrap">
                        <div class="info">
                          <span> 姓名：</span
                          ><span> {{ article.teacherName }}</span>
                        </div>
                        <div class="info left-38">
                          <span>邮箱：</span><span> {{ article.email }}</span>
                        </div>
                      </div>
                      <div class="infoWrap">
                        <div class="info left-38">
                          <span> 职称：</span><span> {{ article.title }}</span>
                        </div>
                        <div class="info">
                          <span> 荣誉：</span><span> {{ article.post }}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="txt">
                    <p v-html="article.content"></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      currpage: "list",
      list: [],
      article: {},
      page: {},
    };
  },
  created() {
    this.loadData();
  },
  methods: {
    loadData: function () {
      axios.get("/api/index/teacher/page", {}).then((d) => {
        if (d.data.code == 200) {
          this.currpage = "list";
          let result = d.data.data;
          this.page = result;
          this.list = result.list;
        }
      });
    },
    toDetail: function (id) {
      axios.get("/api/index/teacher/get?id=" + id).then((d) => {
        this.currpage = "detail";
        this.article = d.data.data;
      });
    },
  },
};
</script>
<style>
.img {
  width: 320px;
  height: 180px;
  margin: 0 auto;
}
.infoWrap {
  height: 30px;
}
.info {
  float: left;
}
.infoDiv {
  width: 280px;
  margin: 0 auto;
}
.left-38 {
  margin-left: 38px;
}
</style>



